@import 'color.less';

.info-box {
  width: 100%;
  height: 200px;
  padding: 12px 20px;
  box-sizing: border-box;
  border: 2px solid #ccc;
  border-radius: 4px;
  background-color: #f8f8f8;
  resize: none;
}

.copy-error-message{
  font-size: 20px;
  font-weight: 700;

    &.success{
      color:@green;
    }

    &.unsuccess{
      color:@red;
    }

}